<template>
    <div class="evaluateCenter">
        <div class="top">
            <div class="chart_tit">
                <img src="../../../assets/img/fkCenter/jiantou.png"/>
                <span>职能部门合规管理水平评价</span>
            </div>
            <div class="chartsContent">
                <bar-chart :seriesData="pjCharts1" :xData="pjxData1" :_zb="dw2"  :legend="legend2"></bar-chart>
            </div>
        </div>
        <div class="center">
            <div class="chart_tit">
                <img src="../../../assets/img/fkCenter/jiantou.png"/>
                <span>直属机构合规管理水平评价</span>
            </div>
            <div class="chartsContent">
                <bar-chart :seriesData="pjCharts2" :xData="pjxData2" :_zb="dw2"  :legend="legend2"></bar-chart>
            </div>
        </div>
        <div class="bottom">
            <div class="chart_tit">
                <img src="../../../assets/img/fkCenter/jiantou.png"/>
                <span>供电分局合规管理水平</span>
            </div>
            <div class="chartsContent">
                <bar-chart :seriesData="pjCharts3" :xData="pjxData3" :_zb="dw3"  :legend="legend3" ref="pj3" ></bar-chart>
            </div>
        </div>
    </div>
</template>

<script>
  import barChart from '../../components/bar-charts'
  import * as echarts from "echarts";
  export default {
    name: "evaluateCenter",
    components:{
      barChart,
    },
    data(){
      return {
        //部门合规管理水平
        pjCharts1:[],
        pjxData1:['办公室', '人力资源部', '党建工作部', '合规监审部', '财务部', '系统规划部', '资产管理部', '市场营销部', '创新与数字化部', '安全监督部'],
        dw1:['单位：分',''],
        legend1:{legend:true},

        //各单位合规管理水平评价
        pjCharts2:[],
        pjxData2:['调控中心', '变电一所', '变电二所', '输电所', '生产监控与试验中心', '项目与供应链中心', '供电服务中心','综合服务中心','电网规划中心'],
        dw2:['单位：分',''],
        legend2:{legend:true},

        //24个分局合规管理水平
        pjCharts3:[],
        pjxData3:[],
        dw3:['单位：分',''],
        legend3:{legend:true},
        myVar:null,
        // pj3:true,
      }
    },
    created() {
      this.getpjCharts1()
      this.getpjCharts2()
      this.getpjCharts3()
    },
    methods:{
      //部门合规管理水平
      getpjCharts1(){
        this.pjCharts1 = [{
          // name: '取证',
          type: 'bar',
          barWidth: 28,
          data: [95,96,97,99,94,98,92,94,98,92],
          itemStyle: {
            normal: {
              color: new echarts.graphic.LinearGradient(0, 1, 0, 0, [{
                offset: 0,
                color: "#007EFF" // 0% 处的颜色
              }, {
                offset: 1,
                color: "#00FFEF" // 100% 处的颜色
              }], false)
            }
          },
        }]
      },
      //各单位合规管理水平评价
      getpjCharts2(){
        this.pjCharts2 = [ {
          // name: '问题数',
          type: 'line',
          symbol: 'circle',//折线点设置为实心点
          symbolSize: 7,   //折线点的大小
          data: [91,89,87,92,83,99,78,95,96],
          itemStyle: {
            color: '#44ecbd',
            shadowColor: '#abffd3',
            shadowBlur: 20,
          },
          areaStyle:{
            normal:{
              //颜色渐变函数 前四个参数分别表示四个位置依次为左、下、右、上
              color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                offset: 0,
                color: 'rgba(93,255,189,1)'
              }, {
                offset: .6,
                color: 'rgba(99,255,201,0.65)'
              },{
                offset: 1,
                color: 'rgba(38,197,254,0.00)'
              }])

            }
          },
          lineStyle: {
            shadowColor: "#5cfbff",
            shadowOffsetX: 0,
            shadowOffsetY: 0,
            opacity: 1,
            shadowBlur: 8,
            type: "solid",
            width: 3
          },
        }]
      },

      //24个分局合规管理水平
      getpjCharts3() {
        let yDataSql = []
        let xDataSql = []
        let yDataSql1 = [97.14, 89.44, 93.32, 91.68, 84.07, 85.84, 91.55, 91.05, 85.84, 91.48, 72.85, 82.93, 89.14, 82.13, 77.06, 83.07, 75.73, 77.49, 72.16, 85.23, 65.84, 82.29, 76.18, 67.87]
        let xDataSql2 = ['大涌','南区','三乡','古镇','南头','东升','阜沙','西区','东区','石岐','坦洲','小榄','民众','黄圃','三角','火炬','港口','南朗','沙溪','五桂山','板芙','东凤','横栏','神湾',]
        let dataObjList = []
        //降序
        for (let i = 0;i<yDataSql1.length;i++){
          dataObjList.push({
            name:xDataSql2[i],
            value:yDataSql1[i]
          })
        }
        dataObjList.sort((a,b)=>{return b.value-a.value})
        dataObjList.forEach(v=>{
          xDataSql.push(v.name)
          yDataSql.push(v.value)
        })

        let yData = []
        let t = 10
        yData = yDataSql.slice(0,t)
        this.pjxData3 = xDataSql.slice(0,t)
        this.myVar = setInterval( ()=> {
          if (t >= yDataSql.length -1){
            clearInterval(this.myVar)
            this.getpjCharts3()
          }else {
            yData.shift();yData.push(yDataSql[t+1])
            this.pjxData3.shift(); this.pjxData3.push(xDataSql[t+1])
            t++
          }
        }, 1200);
        this.pjCharts3 = [{
          // name: '取证',
          type: 'bar',
          barWidth: 28,

          data: yData,
          itemStyle: {
            normal: {
              color: new echarts.graphic.LinearGradient(0, 1, 0, 0, [{
                offset: 0,
                color: "#C27300" // 0% 处的颜色
              }, {
                offset: 1,
                color: "#EEDE5E" // 100% 处的颜色
              }], false)
            }
          },
        }]
      },

      setData3(){

      }
    }
  }
</script>

<style scoped lang="scss">
    .evaluateCenter{
        height: 100%;
        display: flex;
        flex-wrap: wrap;
        .chart_tit {
            height: 38px;
            line-height: 38px;
            font-size: 18px;
            color: #00F8FF;
            font-weight: 700;
            padding: 0 8px;
            >img{
                height: 28px;
            }
            > img, span {
                vertical-align: middle;
                margin-right: 4px;
            }
        }
        .chartsContent{
            height: calc(100% - 38px);
        }
        >div{
            width: 100%;
            height: calc(33% - 12px);
            background: url("../../../assets/img/disposalCenter/hgsppj.png");
            background-size:100% 100%;
        }
        .top{
            margin-bottom: 18px;
            height: calc(34% - 14px);

        }
        .center{

        }
        .bottom{
            margin-top: 18px    ;
        }
    }

</style>